<template>
    <div>
        <template v-if="step == 0">  <!--vue3버전부터 v-if와 v-for 같이 사용 못함.-->
            <Post :InstarData="InstarData[j]" 
                   v-for="(i,j) in InstarData" :key="j"/>
        </template>      
        
        <!-- 필터선택페이지 -->
        <template  v-if="step == 1">
            <div :class="selectFilter+' upload-image'" :style="{ backgroundImage : `url(${urlImg})`}"></div>
            <div class="filters" >
                <FilterBox :urlImg="urlImg" :filter="filter" v-for="filter in InstarFilter" :key="filter" >
                    <template v-slot:a>{{filter}}</template>
                </FilterBox>
            </div>
        </template>

        <!-- 글작성페이지 -->
        <template  v-if="step == 2">
            <div :class="selectFilter+' upload-image'" :style="{ backgroundImage : `url(${urlImg})`}"></div>
            <div class="write">
                <textarea @input="$emit('textWrite', $event.target.value)" class="write-box">write!</textarea>
            </div>
        </template>
    </div>  
</template>

<script>

import Post from './Post.vue';
import FilterBox from './FilterBox.vue';
export default {
    name : 'Container',
    data (){
        return{
            InstarFilter : [ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
                             "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
                             "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"],
            selectFilter : '',    
        }
    },
    mounted (){
        this.emitter.on('sendFilter', (a)=>{
        this.selectFilter = a;
        })
    },
    props : {
        InstarData  : Array,
        step : Number,
        urlImg : String,
        filterInput : String
    },
    components: {
        Post,
        FilterBox
  }
}
</script>

<style>
.upload-image{
width: 100%;
height: 450px;
background: cornflowerblue;
background-size : cover;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;
}
</style>