<template>
    <div  @click="fire" :class="filter + ' filter-item'" :style="{backgroundImage:`url(${urlImg})`}">
        <slot name="a"></slot>  
        <!-- 
            - 여러개 쓰고 싶은 속성값을 name 지정함.
            - 부모가 자식 데이터 쓰고 싶을 때는 slot prop 이용. 
        -->
    </div> 
</template>

<script>
export default {
    name : 'FilterBox',
    props : {
        urlImg : String,
        filter : String,
    },
    methods : {
        fire(){
            this.emitter.emit('sendFilter', this.filter)
        }
    },
}
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color : white;
  background-size: cover;
  background-position : center;
}
</style>