<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <h4>기본Store-> 안녕, {{ $store.state.name }}</h4>
  <h4>computed--> 안녕, {{name}} {{age}} {{likes}} {{내이름}}</h4>
  <h5>난 {{$store.state.age}}</h5>
  <p>{{$store.state.more}}</p>
  <p>methods--> {{now()}} {{count}}</p>
  <p>computed-> {{now2}} {{count}}</p> <!-- 컴퓨티드는 괄호 삭제-->
  <button @click="$store.commit('changeName')">클릭</button>
  <button @click="changeName">클릭2</button>
  <button @click="$store.commit('increaseAge', 10)">올려</button>
  <button @click="$store.dispatch('getData')">더보기</button>
  <button @click="count++">시간 확인</button>
  
  <Container :InstarData="InstarData" :step="step" :urlImg="urlImg" @textWrite="textData = $event" />
  <button v-if="step==0" @click="more" >더보기</button>
  
  <div class="footer" v-if="step==0">
    <ul class="footer-button-plus">
      <input @change="upload" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

</template>

<script>
import Container from './components/Container.vue'
import InstarData from './assets/InstarData.js'
import axios from 'axios'
import { mapMutations, mapState } from 'vuex'


export default {
  name: 'App',
  data (){
    return {
      step : 0, 
      cnt : 0,
      InstarData : InstarData,
      urlImg : '',
      textData : '',
      selectFilter : '',
      count : 0
    }
  },
  mounted (){
      this.emitter.on('sendFilter', (a)=>{
      this.selectFilter = a;
      })
  },
  computed : {  //return 필수기입. 결과를 반환해줘야 함. 무조건.
    name(){
      return this.$store.state.name
    },
    ...mapState(['name', 'age', 'likes']),
    ...mapState({ 내이름 : 'name'}),

    now2(){
      return new Date()
    }
  },
  methods : {
    ...mapMutations(['setMore', 'changeName']),
    now(){
      return new Date()
    },
    publish(){
        var myData =     {
        id : this.id++,  
        name: "Minny",
        userImage: "https://placeimg.com/100/100/animals",
        postImage: this.urlImg,
        likes: 49,
        date: "Apr 4",
        liked: false,
        content: this.textData,
        filter: this.selectFilter
      };
      console.log(myData);
      this.InstarData.unshift(myData);  //unshift 왼쪽에 자료 넣어주기.
      this.step = 0;
    },
    more(){
      //axios.post('URL', {name : 'kim'}).then().catch(err=>{}) 성공하면 .then(코드), 실패하면 .catch(코드) 실행.
      axios.get(`https://codingapple1.github.io/vue/more${this.cnt}.json`)  //``를 이용하자!!!!!!!! ${} 형식으로 문자 중간에 삽입 가능.
           .then(result=>{  
            //function()를 쓰면 this를 재정의하는데, =>로 쓰면 그대로 가져와서 사용함.그리고 값이 하나면 ()까지 생략 가능.
                console.log(result);
                console.log(result.data);
                this.InstarData.push(result.data);
                this.cnt++;   
           })
    },
    upload(e){
      let a = e.target.files;
      console.log(a[0].type);
      let url = URL.createObjectURL(a[0]);
      console.log(url); //blob -> binary(0,1로 구성된) 데이터를 BLOB라는 객체에 담아서 다룸.
      this.urlImg = url;
      this.step = 1;
    }

  },
  components: {
    Container : Container,
  }
}
</script>

