import axios from 'axios';
import { createStore } from 'vuex'

const store = createStore({
  state(){
    return {
        name : 'kim',
        age: 20,
        likes : [30,40,50],
        pushLikes : [false,false,false],
        more : {},
    }
  },
  mutations : {
    setMore(state, data){
        state.more = data 
    },
    changeName(state){
        state.name = 'park'
    },
    increaseAge(state, data){
        state.age+=data;
    },
    selecteLikes(state, value){
        if(state.pushLikes[value] == false){
            state.likes[value]++;
            state.pushLikes[value] = true;
        }else{
            state.likes[value]--;
            state.pushLikes[value] = false;
        }
    },
  },
  actions : {
    getData(context){
        axios.get(`https://codingapple1.github.io/vue/more0.json`)
        .then((a)=>{
            console.log(a.data);
            context.commit('setMore', a.data);
        });
    },
  }
})

export default store;